.flex-center{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.App{text-align:center}#global-container{font-family:"Oleo Script Swash Caps",cursive;font-family:"Quicksand",sans-serif;text-shadow:1px 1px #2e3847;background:#25d2ff;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;color:#fff}#global-container h1{font-size:3em}.game-container{display:grid;grid-template-columns:repeat(3, 100px);grid-gap:10px;gap:10px;grid-auto-rows:100px}.game-container>div{cursor:pointer;font-size:40px;font-weight:bold;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#000;background:#e0ffff;border-radius:4px}.game-container>div:hover{opacity:.8}.game-container .pattern{-webkit-animation-name:shake;animation-name:shake;-webkit-animation-duration:1s;animation-duration:1s;background:#adff2f}.animate{-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:.5s;animation-duration:.5s}@-webkit-keyframes fadeIn{from{color:rgba(0,0,0,0);opacity:.5}to{color:#000;opacity:1}}@keyframes fadeIn{from{color:rgba(0,0,0,0);opacity:.5}to{color:#000;opacity:1}}@-webkit-keyframes pattern{from{opacity:.5}to{opacity:1}}@keyframes pattern{from{opacity:.5}to{opacity:1}}@-webkit-keyframes shake{0%{-webkit-transform:translate(1px, 1px) rotate(0deg);transform:translate(1px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-1deg);transform:translate(-1px, -2px) rotate(-1deg)}20%{-webkit-transform:translate(-3px, 0px) rotate(1deg);transform:translate(-3px, 0px) rotate(1deg)}30%{-webkit-transform:translate(3px, 2px) rotate(0deg);transform:translate(3px, 2px) rotate(0deg)}40%{-webkit-transform:translate(1px, -1px) rotate(1deg);transform:translate(1px, -1px) rotate(1deg)}50%{-webkit-transform:translate(-1px, 2px) rotate(-1deg);transform:translate(-1px, 2px) rotate(-1deg)}60%{-webkit-transform:translate(-3px, 1px) rotate(0deg);transform:translate(-3px, 1px) rotate(0deg)}70%{-webkit-transform:translate(3px, 1px) rotate(-1deg);transform:translate(3px, 1px) rotate(-1deg)}80%{-webkit-transform:translate(-1px, -1px) rotate(1deg);transform:translate(-1px, -1px) rotate(1deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg);transform:translate(1px, 2px) rotate(0deg)}100%{-webkit-transform:translate(1px, -2px) rotate(-1deg);transform:translate(1px, -2px) rotate(-1deg)}}@keyframes shake{0%{-webkit-transform:translate(1px, 1px) rotate(0deg);transform:translate(1px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-1deg);transform:translate(-1px, -2px) rotate(-1deg)}20%{-webkit-transform:translate(-3px, 0px) rotate(1deg);transform:translate(-3px, 0px) rotate(1deg)}30%{-webkit-transform:translate(3px, 2px) rotate(0deg);transform:translate(3px, 2px) rotate(0deg)}40%{-webkit-transform:translate(1px, -1px) rotate(1deg);transform:translate(1px, -1px) rotate(1deg)}50%{-webkit-transform:translate(-1px, 2px) rotate(-1deg);transform:translate(-1px, 2px) rotate(-1deg)}60%{-webkit-transform:translate(-3px, 1px) rotate(0deg);transform:translate(-3px, 1px) rotate(0deg)}70%{-webkit-transform:translate(3px, 1px) rotate(-1deg);transform:translate(3px, 1px) rotate(-1deg)}80%{-webkit-transform:translate(-1px, -1px) rotate(1deg);transform:translate(-1px, -1px) rotate(1deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg);transform:translate(1px, 2px) rotate(0deg)}100%{-webkit-transform:translate(1px, -2px) rotate(-1deg);transform:translate(1px, -2px) rotate(-1deg)}}.reset-button{padding:10px 20px;margin-top:20px;border-radius:2px;cursor:pointer}.App-logo{height:40vmin;pointer-events:none}.won{padding:10px;font-weight:bold;font-size:20px}.turn{font-weight:bold;font-size:20px;padding:10px}@media(prefers-reduced-motion: 